<template>
  <div class="root">
    <!---->
    <el-form :inline="true" :model="searchForm" class="search-form">
      <el-form-item label="">
        <el-input v-model="searchForm.username" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="searchForm.enabled" placeholder="">
          <el-option
            v-for="item in [{label:''},{label:'',value: true},{label:'',value: false}]"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchIt"></el-button>
      </el-form-item>
    </el-form>

    <!---->
    <div class="tool-bar">
      <el-button-group>
        <el-button type="primary" icon="edit" @click="modifyOne"></el-button>
        <el-button type="primary" icon="share" @click="createOne"></el-button>
        <el-button type="primary" icon="delete" @click="deleteOne"></el-button>
        <el-button type="primary" icon="delete" @click="modifyRole"></el-button>
      </el-button-group>
    </div>

    <!---->
    <el-table :data="page.content" highlight-current-row @current-change="selectRow" stripe style="width: 100%"
              align="left">
      <el-table-column
        type="index"
        :index="customIndex">
      </el-table-column>
      <el-table-column prop="username" label="" width="180"></el-table-column>
      <el-table-column prop="enabled" label="" width="180">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.enabled ? 'primary' : 'warning'"
            close-transition>{{ scope.row.enabled ? '' : ''}}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.lastLoginTime }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="lastLoginIp" label="IP"></el-table-column>
    </el-table>

    <!---->
    <el-pagination v-show="page.content && page.content.length > 0"
                   :current-page.sync="page.number"
                   :page-size="page.size"
                   layout="total, prev, pager, next"
                   :total="page.totalElements"
                   class="page">
    </el-pagination>

    <user-dialog :visible.sync="userDialogVisible" @submitSuccess="loadMainData" :currentModel="currentModel"
                 :title="userDialogTitle"></user-dialog>

    <user-role-dialog :visible.sync="userRoleDialogVisible" @submitSuccess="loadMainData" :currentModel="currentRow"> </user-role-dialog>
  </div>
</template>

<script>
  import qs from 'qs'
  import UserDialog from "./UserDialog.vue";
  import UserRoleDialog from "./UserRoleDialog.vue";

  export default {
    components: {
      UserRoleDialog,
      UserDialog},
    name: 'user',
    data() {
      return {
        searchForm: {},  //
        page: {},       // 
        currentRow: null, // 
        userDialogVisible: false,
        userRoleDialogVisible: false,
        currentModel: {},
        userDialogTitle: ""
      }
    },
    mounted: function () {
      this.loadMainData();
    },
    methods: {
      // 
      loadMainData: function () {
        this.userDialogVisible = false
        this.userRoleDialogVisible = false

        this.axios.get('/manage/user/list?' + qs.stringify(this.searchForm))
          .then((response) => {
          console.log('response',response)
            this.page = response.data.content.page;
          })
          .catch(err => {
            this.$message.error('' + err, 2)
          })
      },

      // search
      searchIt: function () {
        this.loadMainData()
      },

      // tool-bar
      modifyOne: function () {
        if (this.currentRow == null) {
          this.$message.error('', 2)
          return
        }

        this.userDialogTitle = ""
        this.currentModel = this.currentRow
        this.userDialogVisible = true
      },
      deleteOne: function () {
        if (this.currentRow == null) {
          this.$message.error('', 2)
          return
        }

        this.$confirm(`${this.currentRow.username}, ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.axios.get(`/manage/user/delete/${this.currentRow.id}`)
            .then((response) => {
              this.$message.success('!')
              this.loadMainData()
            })
        })
      },
      createOne: function () {
        this.currentModel = {}
        this.userDialogVisible = true
      },
      modifyRole: function () {
        if (this.currentRow == null) {
          this.$message.error('', 2)
          return
        }
        this.userRoleDialogVisible = true
      },

      // table
      customIndex: function (index) {
        return index * this.page.number + 1
      },

      selectRow: function (val) {
        this.currentRow = val
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../../src/assets/scss/common.scss";
</style>



