<template>
  <div class="root">
    <!---->
    <el-form :inline="true" :model="searchForm" class="search-form">
      <el-form-item label="">
        <el-input v-model="searchForm.userId" placeholder="ID"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="searchForm.address" placeholder=""></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="searchIt"></el-button>
      </el-form-item>
    </el-form>

    <!---->
    <div class="align-left">
      <el-button-group>
        <el-button type="primary" icon="plus" @click="createOne"></el-button>
        <el-button type="primary" icon="edit" @click="modifyOne"></el-button>
        <el-button type="primary" icon="delete" @click="deleteOne"></el-button>
      </el-button-group>
    </div>
    <el-table :data="page.content" ref="singleTable" highlight-current-row @current-change="selectRow" stripe style="width: 100%">
      <el-table-column type="index" width="100"></el-table-column>
      <el-table-column prop="userId" label="" width="width: 50%" ></el-table-column>
      <el-table-column prop="address" label="" width="width: 50%" ></el-table-column>
      <el-table-column prop="isDefault" label="" width="100">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.isDefault=='1' ? 'primary' : 'warning'"
            close-transition>{{ scope.row.isDefault==1 ? '' : ''}}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="state" label="" width="100">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.state=='1' ? 'primary' : 'warning'"
            close-transition>{{ scope.row.state==1 ? '' : ''}}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="valid" label="" width="100">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.valid=='1' ? 'primary' : 'warning'"
            close-transition>{{ scope.row.valid==1 ? '' : ''}}
          </el-tag>
        </template>
      </el-table-column>
    </el-table>




    <address-dialog :visible.sync="addressDialogVisible" @submitSuccess="loadMainData" :currentModel="currentModel"
                  :title="addressDialogTitle"></address-dialog>


  </div>
</template>

<script>
  import qs from 'qs'
  import AddressDialog from "./AddressDialog";

  export default {
    components: {
      AddressDialog,
    },
    name: "address",
    data() {
      return {
        searchForm: {},  //
        page: {},       // 
        currentRow: null, // 
        addressDialogVisible: false,
        currentModel: {},
        addressDialogTitle: ""
      }
    },
    mounted: function () {
      this.loadMainData();
    },

    methods: {
      // 
      loadMainData: function () {
        this.addressDialogVisible = false

        this.axios.get('/manage/address/list?' + qs.stringify(this.searchForm))
          .then((response) => {
            console.log('response', response)
            this.page = response.data.content.page;
          })
          .catch(err => {
            this.$message.error('' + err, 2)
          })
      },

      // search
      searchIt: function () {
        this.loadMainData()
      },

      // tool-bar
      modifyOne: function () {
        if (this.currentRow == null) {
          this.$message.error('', 2)
          return
        }

        this.addressDialogTitle = ""
        this.currentModel = this.currentRow
        this.addressDialogVisible = true
      },
      deleteOne: function () {
        if (this.currentRow == null) {
          this.$message.error('', 2)
          return
        }

        this.$confirm(`${this.currentRow.address}, ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.axios.get(`/manage/address/deleteAddress/${this.currentRow.id}`)
            .then((response) => {
              this.$message.success('!')
              this.loadMainData()
            })
        })
      },
      createOne: function () {
        this.currentModel = {}
        this.addressDialogVisible = true
      },

      customIndex: function (index) {
        return index * this.page.number + 1
      },

      selectRow: function (val) {
        this.currentRow = val
      }
    }
  }
</script>

<style scoped>

</style>



