<template>
  <div class="root">
    <!---->
    <el-form :inline="true" :model="searchForm" class="search-form">
      <el-form-item label="">
        <el-input v-model="searchForm.name" placeholder="" label-width="600px"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchIt"></el-button>
      </el-form-item>
    </el-form>

    <!---->
    <div class="tool-bar">
      <el-button-group>
        <el-button type="primary" icon="edit" @click="modifyOne"></el-button>
        <el-button type="primary" icon="share" @click="createOne"></el-button>
        <el-button type="primary" icon="delete" @click="deleteOne"></el-button>
        <el-button type="primary" icon="edit" @click="modifyAuth"></el-button>
        <el-button type="primary" icon="delete" @click="sync"></el-button>
      </el-button-group>
    </div>

    <!---->
    <el-table :data="page.content" highlight-current-row @current-change="selectRow" stripe style="width: 100%">
      <el-table-column type="index" :index="customIndex"></el-table-column>
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="description" label=""></el-table-column>
    </el-table>

    <!---->
    <el-pagination v-show="page.content && page.content.length > 0"
                   :current-page.sync="page.number"
                   :page-size="page.size"
                   layout="total, prev, pager, next"
                   :total="page.totalElements"
                   class="page">
    </el-pagination>

    <role-dialog :visible.sync="roleDialogVisible" @submitSuccess="loadMainData" :currentModel="currentModel"
                 :title="roleDialogTitle"></role-dialog>

    <authority-dialog :visible.sync="authDialogVisible" @submitSuccess="loadMainData" :currentModel="currentRow"
                 :title="authDialogTitle"></authority-dialog>
  </div>
</template>

<script>
  import qs from 'qs'
  import RoleDialog from "./RoleDialog.vue";
  import AuthorityDialog from "./AuthorityDialog.vue";
  import { businessRouters } from "@/router/index"

  export default {
    components: {
      AuthorityDialog,
      RoleDialog
    },
    name: 'role',
    data() {
      return {
        searchForm: {},  //
        page: {},       // 
        currentRow: null, // 
        roleDialogVisible: false,
        currentModel: {},
        roleDialogTitle: "",

        authDialogVisible: false,
        authDialogTitle: ""
      }
    },
    mounted: function () {
      this.loadMainData();
    },
    methods: {
      // 
      loadMainData: function () {
        this.roleDialogVisible = false
        this.authDialogVisible = false
        this.axios.get('/manage/role/list?' + qs.stringify(this.searchForm))
          .then((response) => {
            this.page = response.data.content.page;
          })
          .catch(err => {
            this.$message.error('' + err, 2)
          })
      },

      // search
      searchIt: function () {
        this.loadMainData()
      },

      // tool-bar
      modifyOne: function () {
        if (this.currentRow == null) {
          this.$message.error('', 2)
          return
        }

        this.roleDialogTitle = ""
        this.currentModel = this.currentRow
        this.roleDialogVisible = true
      },
      deleteOne: function () {
        if (this.currentRow == null) {
          this.$message.error('', 2)
          return
        }

        this.$confirm(`${this.currentRow.name}, ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.axios.get(`/manage/role/delete/${this.currentRow.id}`)
            .then((response) => {
              this.$message.success('!')
              this.loadMainData()
            })
        })
      },
      createOne: function () {
        this.currentModel = {}
        this.roleDialogVisible = true
      },
      modifyAuth: function () {
        if (this.currentRow == null) {
          this.$message.error('', 2)
          return
        }
        console.log('currentRow',this.currentRow)

        this.authDialogVisible = true
      },
      sync(){
        this.axios.post('/manage/resource/sync',businessRouters)
          .then((r) => {
            this.$message.success('!', 2)
          })
      },

      // table
      customIndex: function (index) {
        return index * this.page.number + 1
      },

      selectRow: function (val) {
        this.currentRow = val
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "src/assets/scss/common.scss";
</style>



