<template>
  <div class="root">
    <!---->
    <el-form :inline="true" :model="searchForm" class="search-form">
      <el-form-item label="">
        <el-input v-model="searchForm.goodsName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="searchForm.typeId" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="searchForm.state" placeholder="">
          <el-option
            v-for="item in [{label:''},{label:'',value: 1},{label:'',value: 0}]"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="searchIt"></el-button>
      </el-form-item>
    </el-form>

    <!---->
    <div class="align-left">
      <el-button-group>
        <el-button type="primary" icon="plus" @click="createOne"></el-button>
        <el-button type="primary" icon="edit" @click="modifyOne"></el-button>
        <el-button type="primary" icon="delete" @click="deleteOne"></el-button>
      </el-button-group>
    </div>
    <el-table :data="page.content" ref="singleTable" highlight-current-row @current-change="selectRow" stripe style="width: 100%">
      <el-table-column type="index" width="100"></el-table-column>
      <el-table-column prop="goodsName" label="" width="width: 50%" ></el-table-column>
      <el-table-column prop="typeName" label="" width="width: 50%" ></el-table-column>
      <el-table-column prop="price" label="" width="width: 50%" ></el-table-column>
      <el-table-column prop="remark" label="" width="width: 50%" ></el-table-column>
      <el-table-column prop="stock" label="" width="width: 50%" ></el-table-column>
      <el-table-column prop="volumeDay" label="" width="width: 50%" ></el-table-column>
      <el-table-column prop="volumeMonth" label="" width="width: 50%" ></el-table-column>
      <el-table-column prop="volumeYear" label="" width="width: 50%" ></el-table-column>

      <el-table-column prop="state" label="" width="100">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.state=='1' ? 'primary' : 'warning'"
            close-transition>{{ scope.row.state==1 ? '' : ''}}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="valid" label="" width="100">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.valid=='1' ? 'primary' : 'warning'"
            close-transition>{{ scope.row.valid==1 ? '' : ''}}
          </el-tag>
        </template>
      </el-table-column>
    </el-table>


    <!---->
    <el-pagination v-show="page.content.length > 0"
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page.sync="currentPage"
                   :page-sizes="[5, 10, 15, 20]"
                   :page-size="pageSize"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalCount"
                   class="page">
    </el-pagination>

    <goods-dialog :visible.sync="goodsDialogVisible" @submitSuccess="loadMainData" :currentModel="currentModel"
                       :title="goodsDialogTitle"></goods-dialog>


  </div>
</template>

<script>
  import qs from 'qs'
  import GoodsDialog from "./GoodsDialog";

  export default {
    components: {
      GoodsDialog,
    },
    name: "goods",
    data() {
      return {
        searchForm: {},  //
        page: {},       // 
        currentRow: null, // 
        goodsDialogVisible: false,
        currentModel: {},
        goodsDialogTitle: ""
      }
    },
    mounted: function () {
      this.loadMainData();
    },

    methods: {
      // 
      loadMainData: function () {
        this.goodsDialogVisible = false

        this.axios.get('/manage/goods/goodsList?' + qs.stringify(this.searchForm))
          .then((response) => {
            console.log('response', response)
            this.page = response.data.content.page;
          })
          .catch(err => {
            this.$message.error('' + err, 2)
          })
      },

      // search
      searchIt: function () {
        this.loadMainData()
      },

      // tool-bar
      modifyOne: function () {
        if (this.currentRow == null) {
          this.$message.error('', 2)
          return
        }

        this.goodsDialogTitle = ""
        this.currentModel = this.currentRow
        this.goodsDialogVisible = true
      },
      deleteOne: function () {
        if (this.currentRow == null) {
          this.$message.error('', 2)
          return
        }

        this.$confirm(`${this.currentRow.goodsName}, ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.axios.get(`/manage/goods/deleteGoods/${this.currentRow.id}`)
            .then((response) => {
              this.$message.success('!')
              this.loadMainData()
            })
        })
      },
      createOne: function () {
        this.currentModel = {}
        this.goodsDialogVisible = true
      },

      customIndex: function (index) {
        return index * this.page.number + 1
      },

      selectRow: function (val) {
        this.currentRow = val
      }
    }
  }
</script>

<style scoped>

</style>



