const validate = {

  // 
  userName: function (label) {
    let content = label || ''

    return [
      {required: true, message: '' + content, trigger: 'blur'},
      {
        validator: (rule, value, callback) => {
          if (!/^[0-9A-Za-z_]{5,15}$/.test(value)) {
            callback(new Error(content + '5~15'))
          } else {
            callback()
          }
        },
        trigger: 'blur'
      }
    ]
  },

  // 
  password: function (label) {
    let content = label || ''

    return [
      {required: true, message: '' + content, trigger: 'blur'},
      {
        validator: (rule, value, callback) => {
          if (value.length < 5) {
            callback(new Error(content + '5~12'))
          } else {
            callback()
          }
        },
        trigger: 'blur'
      }
    ]
  }
}

export { validate }



